*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
body{
    width: 100vw;
    height: 100vh;
}
#root{
    display: flex;
    justify-content: center;
    margin-top: 19%;
}
.error{
    color: red;
    font-weight: 900;
    margin-top: 20px;
}
.form-group input{
margin-top: 10px;
width: 100%;
border-radius: 10px;
height: 30px;
font-size: 18px;
}
button{
    background-color: #0a6ff3;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    width: 70px;
    height: 30px;
    margin-top: 20px;
}
label{
    font-weight: 900;
}